/* Minimal CSS Reset */
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0; }

ol, ul {
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

/* Typography */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 100;
  src: local("Lato"), local("Lato"), url("../libs/fonts/Lato-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: local("Latot"), local("Lato"), url("../libs/fonts/Lato-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: local("Lato"), local("Lato"), url("../libs/fonts/Lato-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 900;
  src: local("Lato"), local("Lato"), url("../libs/fonts/Lato-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: local("Lato"), local("Lato"), url("../libs/fonts/Lato-Light.woff2") format("woff2"), url("../libs/fonts/Lato-Light.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

html {
  font-size: 12px; }
  @media (min-width: 376px) {
    html {
      font-size: 14px; } }
  @media (min-width: 426px) {
    html {
      font-size: 16px; } }
  @media (min-width: 769px) {
    html {
      font-size: 18px; } }
  @media (min-width: 1440px) {
    html {
      font-size: 20px; } }

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  font-family: 'Lato';
  font-weight: 300; }

h1 {
  font-weight: 900; }

.content-grid {
  margin: 0 auto;
  width: calc(100% - 5rem); }
  @media (min-width: 426px) {
    .content-grid {
      width: calc(100% - 5rem); } }
  @media (min-width: 769px) {
    .content-grid {
      width: calc(100% - 10rem); } }
  @media (min-width: 1025px) {
    .content-grid {
      width: calc(100% - 20rem); } }

::-webkit-scrollbar {
  display: none; }

a {
  text-decoration: none; }



